.App {
  text-align: center;
  background-image: url("./assets/woodBG.jpg");
  background-size:auto;
  background-repeat: no-repeat;
  min-height:100vh;
}

#myVideo {
  position:fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
}

.Start {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  margin:auto;
  animation: transitionIn 1s;
}

.App-startName {
  margin:1vh;
  font-size: calc(10px + 10vmin);
  color: white;
  font-weight: bold;
}

.App-startName:hover {
  opacity:50%;
}

.App-startLinks {
  margin:1vh;
  min-height:50px;
  min-width:50px;
  height:15%;
  width:15;
}

.App-startLinks:hover {
  opacity:50%;
}

.App-frontLink {
  text-decoration: none;
  text-shadow: 5px 0px 0px #000000;
}

.App-header {
  background-color: #879dca00;
  height: 25vh;
  display: flex;
  flex-direction: row;
  color: white;
}

.App-headText {
  font-size: calc(10px + 2vmin);
  color: white;
  font-weight: bold;
}

.App-nameBox {
  text-decoration: none;
  font-weight: bold;
  justify-content: center;
  height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: left;
  font-size: calc(10px + 4vmin);
  color: white;
  margin-left:5vh;
  text-shadow: 5px 0px 0px #000000;
}
.App-nameBox:hover {
  opacity: 50%;
}

.App-contact {
  height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: right;
  font-size: calc(10px + 1vmin);
  color: white;
  margin-left:auto;
  margin-right:5vh;
  text-align:right;
}

.App-links {
  flex-direction: row;
  margin:1vh;
}

.App-linked {
  min-height:50px;
  min-width:50px;
  height:20%;
  width:20%;
  filter:contrast(1.5)
}

.App-linked:hover {
  opacity:50%;
}

.App-body {
  width:98vw;
  justify-content: center;
  display: flex;
  flex-direction: row;
  margin:1vw; 
}

.App-about {
  background-color: #00000069;
  min-height:50vh;
  width:25vw;
  min-width:275px;
  display: flex;
  margin:1vh;
  flex-direction: column;
}

.App-bodyHeader {
  color: #ffffff;
  text-decoration: none;
  margin:1vh;
  font-weight: bold;
  font-size: calc(12px + 2vmin);
}

.App-startHeader {
  color: #000000;
  text-decoration: none;
  margin:1vh;
  font-weight: bold;
  font-size: calc(12px + 2vmin);
  text-align: left;
}

.App-startHeader:hover {
  opacity:50%;
}

.App-aboutBody {
  display:flex;
  flex-direction: row;
  margin:1vh;
  margin-right:5vh;
  text-align: left;
  font-size: calc(10px + 1vmin);
}

.App-panelHead {
  background-color: rgba(71, 71, 71, 0.274);
  width:100%;
}

.App-photo {
  width:15vw;
  height:15vw;
  max-width:400px;
  max-height:400px;
}

.App-projects {
  margin-left:5vh;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  color: #ffffff;
}

.my-node-enter {
  transform:translateX(-110%);
}
.my-node-enter-active {
  transform: translateX(0%);
  transition: all 1000ms;
}
.my-node-exit {
  transform: translateX(0%);
  
}
.my-node-exit-active {
  transform: translateX(-110%);
  transition: all 1000ms;
}

.App-spaceHold {
  min-height: 21vh;
}

.App-page {
  display: flex;
  flex-direction: column;
}


.App-animation {
  animation: transitionIn 1s;
}

/*making some animations*/
@keyframes transitionIn{
  from {
    opacity: 0;
    transform:translateY(-100%) 
  }

  to {
    opacity: 1;
    transform:translateY(0) 
  }
}

ul {
  padding: 0;
}